<template>
      <v-dialog
        width="fit-content"
        v-model="dialog"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="dateRangeText"
            :label="label"
            prepend-icon="mdi-calendar"
            @click:prepend="dialog = true"
            outlined
            dense
            readonly
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker
          v-model="dates"
          reactive
          range
          scrollable
          @change="sendData"
          @input="incrementClicks"
        >
        </v-date-picker>
      </v-dialog>
</template>


<script>
  import { bus } from '../main'

  export default {
    props: ['label','defaultDateStart','defaultDateEnd'],
    data: () => ({
      clicks: 0,
      dates: [],
      dialog: false,
    }),
    methods: {
      incrementClicks(){
        this.clicks++
      },
      sendData() {
        console.log('clicked')
        if(!this.isDateComboValid){
          this.swapDates()
        }
        bus.$emit('sendPickedDates', this.dates)
      },
      setDates(){
        this.dates = [this.defaultDateStart, this.defaultDateEnd]
      },
      formatDate(date){
        if(!date) return null

        const [year, month, day] = date.split('-')
        return `${day}/${month}/${year}`
      },
      swapDates(){
        this.dates = [this.dates[1], this.dates[0]]
      }
    },
    watch: {
      clicks(){
        if(this.clicks == 2){
          this.clicks = 0
          this.dialog = false
        }
      },
      dates: function(){
        //this.sendData(this.dates)
      },
      defaultDateStart: function(){

        this.setDates()
      },
      defaultDateEnd: function(){

        this.setDates()
      },
      dialog: function(){
        if(!this.dialog){
          if(this.dates[0] && !this.dates[1]){
          bus.$emit('sendPickedDates', [this.dates[0],this.dates[0]])

          }
        }
      }
    },
    computed: {
      isDateComboValid(){
        if(!this.dates[1]){
          return true
        }
        else{
          const [startYear, startMonth, startDay] = this.dates[0].split('-')
          const [endYear, endMonth, endDay] = this.dates[1].split('-')

          if(startYear > endYear){
            return false
          } 
          if(startMonth > endMonth){
            return false
          } 
          if(startDay > endDay){
            return false
          } 
          return true
        }
      },
      dateRangeText() {
        if((this.dates[0] === this.dates[1]) && (new Date().toISOString().substr(0,10) === this.dates[0])) {
          return 'Today'
        } else if (this.dates[0] === this.dates[1]){
          return this.formattedStartDate
        } else {
          return [this.formattedStartDate, this.formattedEndDate].join(' ~ ')
        }

      },
      formattedStartDate() {
        return this.formatDate(this.dates[0])
      },
      formattedEndDate(){
        return this.formatDate(this.dates[1])
      },
      isSecondClick() {
        return this.dates.length === 2;
      }
    },
    created(){
        this.dates = [this.defaultDateStart, this.defaultDateEnd]
    }
  }
</script>